}
.slider::before {
  background-image: url(https://static.iviva.com/images/Udhayimages/moon.png);
  content: "";
  width: 12px;
  height: 12px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  bottom: -1.8em;
  left: -4px;
  background-repeat: no-repeat;
  background-position: center center;
  transform: rotate(180deg);
}
.slider::after {
  background-image: url(https://static.iviva.com/images/Udhayimages/sun.png);
  content: "";
  width: 20px;
  height: 20px;
  display: block;
  background-size: 100% auto;
  position: absolute;
  top: -2.5em;
  left: -8px;
  background-repeat: no-repeat;
  background-position: center center;
}
.slider span {
  width: 20px;
  height: 20px;
  display: inline-block;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  left: -9px;
  bottom: 0.7em;
  box-shadow: 0px 0px 3px #ffffff;
  transition: bottom 0.4s ease;
}

.slider.active span {
  bottom: 16vh;
